.home-trend-sub-block-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}
.home-trend-sub-block-wrap-left {
  width: 80%;
  height: 100%;
  padding: 100px 0 50px 0;
}
.home-trend-sub-block-wrap-right {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  width: 20%;
  height: 100%;
  padding-left: 40px;
}
.home-trend-sub-block-wrap-right p {
  font-size: 20px;
  margin-top: 20px;
}
.home-trend-img {
  width: 250px;
  border-radius: 35px;
}
#btn-home-trend {
  width: 200px;
  height: 50px;
  border: 0;
  border-radius: 50px;
  font-size: 24px;
  font-weight: bold;
  margin-top: 35px;
  background-color: #e9f7d2;
  color: #333;
}
#btn-home-trend:hover {
  background-color: #f3f9e9;
}
#home-trend-img-white {
  position: absolute;
  left: 70px;
  top: 2430px;
  /*animation: 2s white_flower ease-in-out infinite;*/
