.home-reason-sub-block-container {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  height: 75vh;
}
.home-reason-sub-block-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.home-reason-sub-block {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: flex-start;
  background-color: rgba(64, 87, 75, 0.87);
  border-radius: 35px;
  height: 34vh;
  width: 760px;
  padding: 40px;
}
#HomeReason h3 {
  color: white;
  font-size: 24px;
}
#HomeReason p {
  font-size: 18px;
}
.home-reason-sub-block-element-img {
  align-self: center;
}
.home-reason-img {
  width: 250px;
  height: 250px;
  border-radius: 35px;
  margin-left: 30px;
}
