#PlantHubMain {
  font-size: 22px;
}
.ph-layout-table-content-wrap {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
}
#ph-filter-title span::before,
#ph-keywords span::before {
  display: inline-block;
  content: "";
  height: 100%;
  vertical-align: middle;
}
#ph-filter-title {
  background-color: #338e6c;
  color: white;
  width: 270px;
  height: 53px;
  padding: 0 10px;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
#ph-filter-title span:first-child {
  font-weight: bold;
}
#ph-filter-title span:last-child {
  font-size: 16px;
}
#ph-filter-title span:last-child:hover {
  font-weight: bold;
  cursor: pointer;
}
#ph-keywords {
  background-color: #edeef2;
  color: #333;
  width: 68vw;
  min-width: 488px;
  height: 53px;
  padding: 0 10px;
  border-radius: 10px;
  font-weight: bold;
  margin-left: 10px;
  margin-right: 10px;
}
#ph-keywords span {
  color: #338e6c;
}
/*collapse*/
.ant-collapse,
.ant-collapse-item {
  border: 0;
}
.ant-collapse {
  margin-top: 10px;
  width: 270px;
  color: #333;
}
.ph-collapse-panel {
  background-color: #edeef2;
  font-size: 16px;
}
/*list*/
#ph-list {
  margin-top: 10px;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: nowrap;
}
#ph-list .ph-card {
  width: 68vw;
  min-width: 488px;
  margin-bottom: 15px;
  margin-right: 10px;
  border: 0;
  box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.2);
}
#ph-list .ph-card:hover {
  cursor: pointer;
  box-shadow: -1px -1px 5px rgba(51, 142, 108, 0.1), -2px 5px 10px rgba(51, 142, 108, 0.4);
}
#ph-list .ph-card .ant-card-body {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
#ph-list .ph-card p:first-child {
  font-weight: bold;
  font-size: 16px;
  margin-left: 3vw;
  width: 40vw;
}
#ph-list .ph-card p:nth-child(2) {
  width: 40vw;
}
#ph-list .ph-card p:nth-child(3) {
  width: 25vw;
}
#ph-list .ph-card .ant-card-body {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
}
/*page*/
.ant-pagination {
  right: 0;
  margin-left: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 10px;
}
/*drawer*/
#ph-close-mask svg {
  width: 130px;
  height: 130px;
}
#ph-close-mask {
  position: fixed;
  transform: translate(-300%, 250%);
  display: none;
}
.ant-drawer span {
  color: #338e6c;
}
.ant-drawer h1 {
  font-size: 40px;
}
.ant-drawer h2 {
  font-size: 24px;
}
.ant-drawer p,
.ant-drawer span {
  font-size: 16px;
}
.ant-drawer h1,
.ant-drawer h2,
.ant-drawer p,
.ant-drawer span {
  font-weight: bold;
}
.ant-drawer h2 {
  margin-bottom: 20px;
}
#ph-drawer-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 80%;
}
.ph-plant-detail {
  margin-top: 200px;
  width: 450px;
}
#ph-drawer-content-img {
  background: url(../../assets/plants/test.jpg);
}
/*filter*/
.ph-collapse-panel {
  border: 0 !important;
  border-radius: 10px;
}
.ph-collapse-panel .check-box {
  margin: 5px 0 !important;
}
.ph-collapse-panel .ant-collapse-header {
  font-weight: bold;
}
.ph-collapse-panel .ant-collapse-content-box {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  overflow-y: auto;
  max-height: 160px;
}
@media only screen and (min-width: 1367px) and (max-width: 1600px) {
  #PlantHubMain .ant-drawer-content-wrapper {
    width: 600px;
  }
}
@media only screen and (min-width: 769px) and (max-width: 1024px) {
  .ph-plant-detail {
    margin-top: 0;
  }
}
@media only screen and (min-width: 376px) and (max-width: 414px) {
  #ph-list .ph-card,
  #ph-keywords,
  #ph-list {
    min-width: 414px;
    margin-left: 0;
    margin-right: 0;
  }
  .ant-pagination {
    width: 375px;
    margin-right: 0;
    margin-left: 30px;
  }
  #ph-list {
    width: 414px;
  }
  .check-box {
    padding-bottom: 10px !important;
  }
}
@media only screen and (max-width: 375px) {
  #ph-list .ph-card,
  #ph-keywords,
  #ph-list {
    min-width: 376px;
    margin-left: 0;
    margin-right: 0;
  }
  .ant-pagination {
    width: 375px;
    margin-right: 0;
  }
  #ph-list {
    width: 375px;
  }
  .check-box {
    padding-bottom: 10px !important;
  }
}
